<template>
  <div>
    <v-select
      variant="solo"
      style="margin-top: 10px;"
      v-model="currentSelect"
      label="Прием пищи"
      :items=itemsSelect></v-select>
    <v-card-text>
      <v-text-field
        v-model="searchStr"
        append-inner-icon="mdi-magnify"
        density="compact"
        label="Поиск"
        variant="solo"
        hide-details
        single-line></v-text-field>
    </v-card-text>
    <v-expansion-panels class="my-4" variant="popout">
      <v-expansion-panel
        v-for="el in dataSort" :key="el.id"
        :title="el.title + ' (' + el.meal + ')'"
        :text="el.text"
      />
    </v-expansion-panels>
  </div>
</template>

<script>

export default {
  name: 'MenuMasha',

  data: () => ({
    searchStr: '',
    currentSelect: 'Все вместе',
    itemsSelect: ['Все вместе', 'Завтрак','Обед', 'Ужин'],
    dataOrigin: [
      {
        id:1,
        meal: 'Завтрак',
        title:'Овсяноблин с рыбой и овощами',
        text: 'bla-bla-bla',
        preparation: 'bla-bla-bla'
      },
      {
        id:2,
        meal: 'Обед',
        title:'Куриные бёдра с овощами, карри и булгуром',
        text: 'bla-bla-bla2',
        preparation: 'bla-bla-bla2'
      },
      {
        id:3,
        meal: 'Ужин',
        title:'Карри с яблоками и нутом',
        text: 'bla-bla-bla3',
        preparation: 'bla-bla-bla3'
      }
    ]
  }),
  computed: {
    dataSort() {
      let dataCopy = JSON.parse(JSON.stringify(this.dataOrigin))
      switch (this.currentSelect) {
        case 'Все вместе':
          break
        case 'Завтрак':
          dataCopy = dataCopy.filter((el)=>el.meal === this.currentSelect)
          break
        case 'Обед':
          dataCopy = dataCopy.filter((el)=>el.meal === this.currentSelect)
          break
        case 'Ужин':
          dataCopy = dataCopy.filter((el)=>el.meal === this.currentSelect)
          break
      }
      if (this.searchStr.length > 0) {
        dataCopy = dataCopy.filter((el)=>el.title.toLowerCase().includes(this.searchStr))
      }
      return dataCopy
    }
  }
}
</script>
